import { useState } from 'react';

import { useADAInput } from '../shared/hooks/input';
import { Checkbox } from '../shared/components/Checkbox';
import { Input } from '../shared/components/Input';
import { Dropdown } from '../shared/components/Dropdown';
import { withGA, withClickTracking } from '../shared/libs/ga';
import { Button } from '../shared/components/Button';

const TrackedButton = withClickTracking(Button, 'click', {
    event_category: 'test',
    event_label: 'bla-bla',
    value: 'hi',
});

const Page = () => {
    const [checked, setChecked] = useADAInput(false, state => !state);
    const [dropdownValue, setDropdownValue] = useState(null);

    const selectValue = value => setDropdownValue(value);

    return (
        <>
            <div className="container">
                <h1 tabIndex="0">Welcome to ARIA demo</h1>
                {/* <TrackedButton onClick={() => console.log('Hi')} /> */}
                <article tabIndex="0">
                    <p>
                        Accessible Rich Internet Applications (ARIA) is a set of
                        attributes that define ways to make web content and web
                        applications (especially those developed with
                        JavaScript) more accessible to people with disabilities.
                    </p>
                </article>
                <section>
                    <header tabIndex="0">
                        <h2>Simple case. Labeling a component</h2>
                    </header>

                    <Input label="This component was labeled by aria-label" />
                    <Input
                        id="1"
                        label="This component was labeled by aria-labelledby"
                    />
                </section>
                <section>
                    <h2 tabIndex="0">
                        Complex case: using aria attributes to create custom
                        controls
                    </h2>
                    <Checkbox
                        checked={checked}
                        label="This checkbox was made using role and aria- attributes"
                        onChange={setChecked}
                    />
                    <Dropdown
                        values={[11, 22, 33, 44]}
                        selectedValue={dropdownValue}
                        label="custom dropdown"
                        setValue={selectValue}
                    />
                </section>
                <a href="https://w3c.github.io/using-aria/">
                    See more on w3c.github.io
                </a>
                <style jsx global>{`
                    body {
                        background-color: #bc986a;
                        box-sizing: border-box;
                    }

                    .container {
                        width: 1024px;
                        margin: 0 auto;
                    }
                `}</style>
            </div>
        </>
    );
};

export default withGA(Page, 'My index page');
